import React, { useState, useEffect } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.css';

let Box = styled.div`
  padding : 20px;
`;
let Title = styled.h4`
  font-size : 25px;
  color : ${ props => props.color };
`;
  
function Detail(props) {
     let [alert, alertChange] = useState(true);
     let [inputData, inputDataChange] = useState();

    useEffect(()=>{     // 컴포넌트가 Mount 또는 Update 되었을 때, 특정 코드를 실행 시킬 수 있다.
      let Timer =  setTimeout(()=>{ alertChange(false); }, 2000);
      return ()=>{ clearTimeout(Timer) } // 타이머 해제 스킬
    },[]); // 이렇게 넣어주면 alert라는 State가 변경이 될 때만 실행이된다.

    let { id } = useParams();
    let opencontent = props.shoes.find((content)=>{
        return content.id == id
    })
    let history = useHistory();
    return (
      <div className="container">
        <Box>
          <Title className="red">상세페이지</Title>
        </Box>
        { alert === true ? <Alert /> : null }
        <div className="row">
          <div className="col-md-6">
            <img src={"https://codingapple1.github.io/shop/shoes"+ (opencontent.id+1) +".jpg"} width="100%" />
          </div>
          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{opencontent.title}</h4>
            <p>{opencontent.content}</p>
            <p>{opencontent.price}원</p>
            <Info inventory={props.inventory}></Info>

            <button className="btn btn-danger" onClick={()=>{ props.inventoryChange([9,10,11]) }}>주문하기</button><br/><br/>
            {/* <button className="btn btn-danger" onClick={ ()=>{ history.push('/') } }>뒤로가기</button>  */}
          </div>
        </div>
      </div>
    )
  }

  function Alert() {
    return(
      <div className="my-alert">
      <p>재고가 얼마 남지 않았습니다!</p>
    </div>
    )
  }

  function Info(props) {
    return(
      <p>재고 : {props.inventory[0]}</p>
    )
  }

  export default Detail;